<template>
	<b-container class="my-4 my-lg-5">
		<div class="d-flex justify-content-between align-items-center mb-2">
			<h1 class="h3">Vendors</h1>

			<b-button href="/vendor" class="ml-4" variant="light">
				<font-awesome-icon icon="external-link-alt" fixed-width />
				<span>Vendor App</span>
			</b-button>
		</div>
		<b-row>
			<b-col cols="12" class="mb-4">
				<b-input-group>
					<template #prepend>
						<b-input-group-text class="bg-white border-right-0">
							<font-awesome-icon icon="search" fixed-width />
						</b-input-group-text>
					</template>
					<b-input
						autofocus
						type="text"
						v-model="search"
						placeholder="Filter by name..."
						class="border-left-0"
					/>
					<template #append>
						<b-button @click="search = ''">
							<font-awesome-icon icon="times" fixed-width />
						</b-button>
					</template>
				</b-input-group>
			</b-col>
		</b-row>
		<b-spinner
			v-if="loading"
			label="Loading..."
			variant="primary"
			class="d-block mx-auto"
			style="width: 5em; height: 5em;"
		/>
		<b-alert v-else-if="results.length <= 0" show variant="light" class="p-5 shadow-sm">
			No results found.
		</b-alert>
		<b-list-group v-else class="shadow-sm">
			<b-list-group-item
				action
				button
				v-for="result in results"
				:key="result.id"
				@click="clicked(result)"
				class="d-flex justify-content-between align-items-center"
			>
				<div>
					<div class="font-weight-bold">{{ result.name }}</div>
				</div>
				<div>
					<div class="badge badge-warning" v-if="!result.active">
						Inactive
					</div>
				</div>
			</b-list-group-item>
		</b-list-group>

		<!-- Form Modal -->
		<b-modal v-model="entity.modal" size="xl" scrollable @ok.prevent="submitForm" :ok-disabled="entity.saving">
			<template #modal-title>
				<h6 class="text-muted mb-1">Vendor</h6>
				<h3 v-if="entity.value.name">
					{{ entity.value.name }}
				</h3>
				<h3 v-else class="text-danger">Missing Name</h3>
			</template>
			<edit-form ref="form" :value="entity.value" :busy="entity.saving" @save="save" />
			<template #modal-ok>
				<font-awesome-icon icon="circle-notch" v-if="entity.saving" spin fixed-width class="mr-2" />
				<span>Save Changes</span>
			</template>
		</b-modal>
	</b-container>
</template>

<script>
import { mapGetters } from "vuex";
import EditForm from "@/admins/components/Vendors/Form.vue";

export default {
	name: "AdminViewVendors",
	components: {
		EditForm
	},
	computed: {
		results() {
			return this.vendors.filter(vendor => vendor.name.toUpperCase().includes(this.search.toUpperCase()));
		},
		...mapGetters({
			vendors: "vendors/all",
			loading: "vendors/loadingAll"
		})
	},
	mounted() {
		this.$store.dispatch("vendors/getAll");
	},
	data() {
		return {
			search: "",
			entity: {
				modal: false,
				saving: false,
				value: {}
			}
		};
	},
	methods: {
		clicked(value) {
			this.entity = {
				modal: true,
				saving: false,
				value: value
			};
		},
		submitForm(e) {
			this.$refs.form.submit(e);
		},
		async save(data) {
			try {
				this.entity.saving = true;

				const response = await this.$store.dispatch("vendors/save", data);
				this.entity.modal = false;
			} catch (e) {
				console.error(e);

				alert(e.response.data.message || "Failed to save");
			} finally {
				this.entity.saving = false;

				this.$store.dispatch("vendors/getAll");
			}
		}
	}
};
</script>
